.wrapper {
    margin-top: auto;
    padding: 60px 0;
}
.text {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #21243D;
    text-align: center;
}
.container {
    display: flex;
    gap: 0 12px;
    justify-content: center;
    margin-bottom: 24px;
}
.link {
    width: 24px;
    height: 24px;
}
.icon {
    width: 100%;
    height: 100%;
    object-fit: cover;
}