.wrapper {
    width: 100%;
    position: relative;

}
.error {
    position: absolute;
    top: -16px;
    left: 0;        
    font-size: 12px;
    font-weight: 500;
    color: #e50012;    
}
.textarea {
    width: 100%;
    border: 1px solid #E0E0E0;
    font-size: 13px;
    font-weight: 500;
    color: #7a7a7a;
    outline: none;
    padding: 15px;
    margin-bottom: 30px;
    resize: none;
}